/* styles.css */

/* General Body Styling */
body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background: linear-gradient(to bottom right, #4e54c8, #8f94fb);
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

/* Login Container */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

/* Login Card */
.login-card {
  background: #ffffff;
  padding: 2rem 3rem;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  text-align: center;
  max-width: 400px;
  width: 100%;
}

.login-card h2 {
  margin-bottom: 0.5rem;
  font-size: 1.8rem;
  color: #333;
}

.login-card p {
  font-size: 0.9rem;
  color: #777;
  margin-bottom: 1.5rem;
}

/* Input Fields */
.login-input {
  width: 100%;
  padding: 0.8rem;
  margin: 0.5rem 0;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  outline: none;
}

.login-input:focus {
  border-color: #4e54c8;
}

/* Login Button */
.login-button {
  width: 100%;
  padding: 0.8rem;
  margin-top: 1rem;
  font-size: 1rem;
  font-weight: bold;
  background: #4e54c8;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.login-button:hover {
  background: #3a41a8;
}

/* Error Text */
.error-text {
  color: red;
  font-size: 0.9rem;
  margin-top: 0.5rem;
}

/* Link Text */
.link-text {
  color: #4e54c8;
  cursor: pointer;
  font-weight: bold;
}

.link-text:hover {
  text-decoration: underline;
}

/* Success Text */
.success-text {
  color: green;
  font-size: 0.9rem;
  margin-top: 0.5rem;
  font-weight: bold;
}

/* Query Parameter Styling */
.query-param {
  margin: 10px 0;
}

/* Dropdown & Input Styling */
.query-param select,
.query-param input {
  width: 100%;
  padding: 0.8rem;
  margin: 0.5rem 0;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  outline: none;
}

.query-param select:focus,
.query-param input:focus {
  border-color: #4e54c8;
}

.query-section {
  margin: 20px auto;
  width: 80%;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background: #fafafa;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.query-section .login-input {
  margin-top: 10px;
}
.query-section h2 {
  text-align: center;
  margin-bottom: 10px;
}

.query-section .chart-container {
  height: 300px;
  margin-top: 20px;
}
/* Main Container Styling */
.main-container {
  padding: 20px;
  background: linear-gradient(to bottom right, #4e54c8, #8f94fb); /* Matching the body background */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Grid for Queries */
.query-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Two columns per row */
  gap: 20px; /* Space between grid items */
  margin-top: 20px;
}

/* Individual Query Box Styling */
.query-item {
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  overflow: hidden;
}

/* Headings for Each Query Section */
.query-item h2 {
  font-size: 1.2rem;
  margin-bottom: 10px;
  text-align: center;
}

/* SVG Map Styling */
.svg-container {
  width: 100%;
  height: 600px;
  margin-top: 20px;
  overflow: hidden;
}

.world-map-svg {
  width: 100%;
  height: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.map-container {
  margin-top: 20px;
  overflow: hidden;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 100%;
  max-width: 1200px;
  height: 600px;
}
